<template>
  <div id="magic">
    <div class="fixed">
      <h1 class="super-bold">Magic happens when <span id="word1" class="magic-word">{{word1}}</span> and <span
          class="magic-word" id="word2">{{word2}}</span> are </h1>
    </div>
    <div class="flex-item" style=" padding:5vw;">
        <img src="/static/devphotos/01.png" style="width:25vw;">
    </div>

  </div>

</template>

<script>
  import {
    TweenLite
  } from 'gsap'
  export default {
    mounted() {
      setInterval(() => {
        // if(this.index1<this.words.length && this.index2) 
        TweenLite.to(document.getElementById('word1'), 0.5, {
          color: '#000000',
          onComplete: () => {
            this.index1 += 1;
            this.word1 = this.words[this.index1 % this.words.length];
            TweenLite.to(document.getElementById('word1'), 0.5, {
              color: '#ffffff',
            })
          },
        });
        TweenLite.to(document.getElementById('word2'), 0.5, {
          color: '#000000',
          onComplete: () => {
            this.index2 += 1;
            this.word2 = this.words[this.index2 % this.words.length];
            TweenLite.to(document.getElementById('word2'), 0.5, {
              color: '#ffffff',
            })
          },
        });



      }, 5000)

    },
    data() {
      return {
        words: ["technology", "creativity", "innovation", "progress"],
        word1: 'technology',
        word2: 'creativity',
        index1: 0,
        index2: 1,
      }
    },

    computed: {
      animatedNumber: function () {
        return this.tweenedWord;
      }
    },
    watch: {
      number: function (newValue) {
        TweenLite.to(this.$data, 0.5, {
          tweenedNumber: newValue
        });
      }
    }
  }

</script>

<style scoped>
  .super-bold {
    font-weight: bolder;
    font-size: 5vw;
    width: 50vw;
    min-width: 50vw;
    text-align: left;
    line-height: 1.5;

  }

  .magic-word {
    background: black;
    width: 100px;

  }


  @media screen and (min-width: 2000px) {
    .super-bold {
      font-weight: bolder;
      font-size: 90px;
      width: 25vw;
      margin-left: 200px;


    }
  }


  #magic {
    padding-left: 50px;
    display: flex;
    position:relative;
    top:20vh;
  }

</style>



